.grid {
  display: flex;
  margin: 0;
  padding: 0;
  flex-wrap: wrap;
  margin-left: -25px;
  .grid-item {
    box-sizing: border-box;
    padding-left: 25px;
    width: 25%;
    float: left;
    vertical-align: top;
    a {
      display: block;
      margin: 0 auto;
      padding: 0 15px 80px;
      position: relative;
      background-image: url("../../styles/images/icons/shadow.png");
      background-size: 100% auto;
      background-position: center 90%;
      background-repeat: no-repeat;
      text-decoration: none;
      img {
        display: block;
        margin: 0 auto;
        position: relative;
        top: 0;
        max-width: 100%;
        transition: top 0.5s;
      }
      .productInfo {
        text-align: center;
        text-transform: uppercase;
        font-family: $header;
        margin-left: -15px;
        position: absolute;
        transition: all 0.5s;
        width: 100%;
        bottom: 0px;
        opacity: 0;
        h3 {
          font-size: 12px;
          line-height: 1.4;
          color: black;
          margin-bottom: 0.5em;
        }
        p {
          color:#c5c5c5;
          margin: 0 0 15px 0;
        }
      }
    }
    a:hover {
      background-image: none;
      img {
        top: -30px;
      }
      .productInfo {
        bottom: 50px;
        opacity: 1;
      }
  }
}
}

.homeCollections {
  background: $near-white-background;
  padding-left: 100px;
  border-top: 1px solid #ebebeb;
  border-bottom: 1px solid #ebebeb;
  .innerWrap2 {
    padding: 25px 25px 0 25px;
    width: 100%;
    .cgrid {
      margin-left: -25px;
      padding: 0;
      box-sizing: border-box;
      .grid-item {
        min-height: 1px;
        width: 33.33%;
        padding-left: 25px;
        float: left;
        .collection {
          width: 100%;
          padding-top: 66%;
          padding-left: 0;
          padding-right: 0;
          padding-bottom: 0;
          height: 0;
          display: block;
          margin-bottom: 25px;
          overflow: hidden;
          position: relative;
          .foo {
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            background-position: center;
            background-size: cover;
            transition: transform 0.8s;
          }
          .coll-bg {
            position: absolute;
            left: 0;
            bottom: 0;
            right: 0;
            top: 0;
            background: rgba(255,255,255,0.3);
            opacity: 0;
            transition: all 0.5s;
          }
          .collection_inner {
            position: absolute;
            bottom: 20px;
            padding: 0 30px;
            transition: all 350ms;
            h2 {
              color: white;
              font-family: $headerlight;
              text-transform: uppercase;
              font-size: 24px;
              letter-spacing: 2px;
              line-height: 1.4;
            }
            &.right {
              width: 100%;
              text-align: right;
            }
          }
          &:hover {
            .coll-bg {
              opacity: 1;
            }
          }
          &:hover {
            .foo {
              transform: scale(1.2);
            }
          }
        }
        .journal {
          box-sizing: border-box;
          width: 100%;
          height: 0;
          padding-top: calc(132% + 25px);
          background-size: cover;
          background-position: center;
          position: relative;
          overflow: hidden;
          display: block;
          .blogImage {
            position: absolute;
            top: 0;
            transition: transform 1s;
            width: 100%;
            height:100%;
            background-size: cover;
            background-position: center;
          }
          .blog_border {
            position: absolute;
            left: 30px;
            right: 30px;
            top: 30px;
            bottom: 30px;
            border: 1px solid rgba(255,255,255,0.4);
            .blog_inner {
              position: absolute;
              top: 50%;
              margin-top: -50px;
              width: 100%;
              padding: 0 30px;
              text-align: center;
              font-family: $headerlight;
              h3 {
                color: white;
                font-size: 18px;
                text-transform: uppercase;
                line-height: 1.4;
                letter-spacing: 1px;
              }
              h2 {
                color: white;
                font-size: 40px;
                text-transform: uppercase;
                line-height: 1.4;
                letter-spacing: 2px;
              }
              .slidebtn {
                margin-top: 10px;
                background-color: $pink-btn-text;
                box-shadow: inset 0 0 0 0 #333;
                &:hover {
                  box-shadow: inset 0 -100px 0 0 #333;
                }
              }
            }
          }
          &:hover {
            .blogImage {
              transform: scale(1.2);
            }
          }
        }
      }
    }
    &:after {
    content: '';
    display: table;
    clear: both;
    }
  }
}

