.slidebtn {
  background-color: black;
  display: inline-block;
  padding: 0 20px;
  height: 40px;
  line-height: 40px;
  font-size: 12px;
  font-family: $header;
  width: auto;
  text-decoration: none;
  text-align: center;
  vertical-align: middle;
  cursor: pointer;
  text-transform: uppercase;
  letter-spacing: 2px;
  outline: none;
  border: 0;
  border-radius: 3px;
  box-shadow: inset 0 0 0 0 $pink-btn-text;
  color: white;
  transition: all ease 0.8s;
  &:hover {
    box-shadow: inset 0 -100px 0 0 $pink-btn-text;
  }
}
